.client-container{margin-bottom:5%}.client-container .client{border-radius:10px;margin:calc(var(--element-margin)*10);box-shadow:15px 15px #e3e3e3;background-color:#fff;background-image:url(../../assets/parallax/b5.webp);margin-bottom:0;display:flex;flex-direction:column}.client-container .client>img{width:500px;height:300px}.client-container .client .gallary{display:flex;flex-wrap:wrap}.client-container .client .gallary .image-wrapper{background-color:#fff;margin:12px;border-radius:10px;overflow:hidden}.client-container .client .gallary .image-wrapper img{margin:0;width:150px;height:100px;background-image:url(https://uploads-ssl.webflow.com/63beff5…/63e2afa…_Custom%20Size%20%E2%80%93%205.png);background-position:50%;background-size:cover}.client-container .client .gallary .image-wrapper img:hover{-webkit-transform:scale(1.6);-moz-transform:scale(1.6);-o-transform:scale(1.6);transform:scale(1.6)}.client-container .client .gallary .image-wrapper img{-webkit-transition:all .4s ease-in-out;-moz-transition:all .4s ease-in-out;-o-transition:all .4s ease-in-out;-ms-transition:all .4s ease-in-out}.client-container .client-heading{display:flex;flex-direction:column;margin:0}.client-container .client-heading h1{min-width:100%;font-family:poppins;line-height:.7em;font-weight:300;-webkit-text-stroke:1px #fff}.client-container .client-heading :last-child{opacity:.8}